<h1 id="git" tabindex="-1"><a class="header-anchor" href="#git" aria-hidden="true">#</a> Git</h1>
<p>This is not an actual course given at Jönköping University. Instead, it's an improvised mini course teaching you the basics in Git, something that every programmer should know these days. Git is very useful when you are multiple programmers working on the same project at the same time.</p>
<h2 id="learning-resources" tabindex="-1"><a class="header-anchor" href="#learning-resources" aria-hidden="true">#</a> Learning Resources</h2>
<p>Here is a list of the resources used in this mini course. They will be introduced to you in the lectures.</p>
<ul>
<li>The program <a href="https://git-scm.com/" target="_blank" rel="noopener noreferrer">git<OutboundLink/></a></li>
<li>The book <a href="https://git-scm.com/book/en/v2" target="_blank" rel="noopener noreferrer">Pro Git<OutboundLink/></a> by <em>Scott Chacon</em> and <em>Ben Straub</em> (available for free online in multiple different formats)</li>
<li>The website <a href="http://git-school.github.io/visualizing-git/" target="_blank" rel="noopener noreferrer">Visualizing Git<OutboundLink/></a></li>
<li>The website <a href="https://learngitbranching.js.org/" target="_blank" rel="noopener noreferrer">Learn Git Branching<OutboundLink/></a></li>
<li>The website <a href="https://rogerdudler.github.io/git-guide/" target="_blank" rel="noopener noreferrer">git - the simple guide<OutboundLink/></a></li>
</ul>
<h2 id="lectures" tabindex="-1"><a class="header-anchor" href="#lectures" aria-hidden="true">#</a> Lectures</h2>
<p>There is a progression in the lectures, so you are recommended to take them in order.</p>
<ol>
<li><a href="../../lectures/git-basics">Git Basics</a><br>
Introduces you to what Git is and teaches you how to create and use a local Git repository that exists only on your own computer.</li>
<li><a href="../../lectures/git-branches">Git Branches</a><br>
Explains what branches are and how you can create and work with them in a local Git repository that exists only on your own computer.</li>
<li><a href="../../lectures/git-synching">Git Synching</a><br>
Explains how you can share your work with others by synching commits in your local Git repository with repositories that exist on other computers.</li>
</ol>
